<template>
	<div class="tableBody">
		<table class="tableBody-table" border="1" cellspacing="0" cellpadding="0">
			<thead>
				<tr class="tableBody-tr">
					<th colspan="2" >项目及考核内容</th>
					<th colspan="2"> 分值	</th>
					<th v-for='item in showType' :key='item.id'>{{item}}</th>
				</tr>
			</thead>
			<tbody>
				<tr v-for="item in tableData" :key="item.id" class="tableBody-tr tableBody-tr-two"  >
					<td v-show="item.isHidden" :rowspan="item.rowspan"  class="vertical" >
						{{item.type}}
					</td>
					<td style='max-width: 3rem'>{{item.content}} </td>
					<td>{{item.cate}}</td>
					<td>{{item.score}}</td>
					<td  v-for="i in showTypeS" :key='i.id'>
						<input type="text" name="" :value="item[i]" class="td-input">
					</td>
					
					
				</tr>
				<tr>
					<td colspan="4">得分合计</td>
					<td >10</td>
				</tr>
			</tbody>
		</table>
		<div class="btn">
			<button type="button" class='btn-submit' @click="tipShow()">
				提交
			</button>
		</div>
		<transition name="slide-fade">
			<Tip v-show="tipFlag"></Tip>	
		</transition>
		
	</div>
</template>

<style>
.tableBody{
	width: 100%;
	/*height: 88%;*/
	padding:24px 12px;
	font-size: 16px;
	overflow: hidden;
}
.tableBody-table{
	width: 100%; 
	padding:6px 12px;
	font-size: 16px;
}

.tableBody-tr{
	width: 100%;
	border:1px solid #000;
	font-size: 20px;
	text-align: center;
}

.vertical{
	vertical:center;
}
tr > th{
	max-width:60px;
	padding:4px 0;
	font-size: 30px;
}
tr > td{
	padding:1px 0;
	max-width:60px;
	word-wrap: break-word;
	font-size: 28px;
	text-align: center;
}
.td-text-center{
	text-align: center;
}
.td-input{
	width: 100%;
	height: 100%;
	text-align: center;
	border:none;
	outline: #2c8ee3;
}
/*  按钮 */
.btn{
	width: 100%;
	padding:12px;
}
.btn > .btn-submit{
	outline: none;
	border:none;
	background-image: -webkit-linear-gradient(93deg,#3f51b5,#00bbd4);
	color: #fff;
	background: #1d89ff;
	font-size: 26px;
	font-family: inherit;
	padding: 5px 12px;
	letter-spacing: 20px;
	text-indent: 10px;
	border-radius: 6px;
	float:right;
}
.slide-fade-enter-active {
	transition: all .3s ease;
}
.slide-fade-leave-active {
	transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active for below version 2.1.8 */ {
	transform: translateX(50%);
	opacity: 0;
}

</style>

<script>
	import Tip from './tip.vue'
	export default {
		name: 'tableBody',
		components:{Tip},
		data(){
			return{
				msg:'this is tableBody.vue test',
				tableData:[
				{
					"id":"1","type":"出勤考核5%","content":"全勤","cate":"优秀","score":"5","zsld":"1","qtld":"2","bmzj":"3",
					"rowspan":"","isHidden":false
				},
				{
					"id":"2","type":"出勤考核5%","content":"偶尔请假、迟到（一次扣一分）","cate":"优秀","score":"5","zsld":"1","qtld":"2","bmzj":"3","rowspan":"","isHidden":false
				},
				{
					"id":"3","type":"日常管理5%","content":"能按照公司规定完成打卡、填写考核表、绩效考核等相关工作","cate":"优秀","score":"5","zsld":"1","qtld":"2","bmzj":"3",
					"rowspan":"","isHidden":false
				},
				{
					"id":"4","type":"出勤考核5%","content":"经常迟到、请假或旷工情况","cate":"优秀","score":"5","zsld":"1","qtld":"2","bmzj":"3","rowspan":"","isHidden":false
				},
				{
					"id":"5","type":"日常管理5%","content":"出现未能按照公司规定完成打卡、填写考核表、绩效考核等相关工作三次以内的","cate":"优秀","score":"5","zsld":"1","qtld":"2","bmzj":"3","rowspan":"","isHidden":false
				},{
					"id":"6","type":"日常管理5%","content":"出现未能按照公司规定完成打卡、填写考核表、绩效考核等相关工作三次以上的","cate":"优秀","score":"5","zsld":"1","qtld":"2","bmzj":"3","rowspan":"","isHidden":false
				},{
					"id":"7","type":"责任感15%","content":"信守职责，勇于承担责任，主动维护公司利益。工作细致、严谨，热情主动完成岗位本职工作，能很好的完成任务","cate":"优秀","score":"5","zsld":"1","qtld":"2","bmzj":"3","rowspan":"","isHidden":false
				},{
					"id":"8","type":"责任感15%","content":"工作努力，为实现目标竭尽全力态度积极。勇于挑战、不畏困难，能较好地完成任务","cate":"优秀","score":"5","zsld":"1","qtld":"2","bmzj":"3","rowspan":"","isHidden":false
				},{
					"id":"9","type":"责任感15%","content":"有责任心，能自动自发地完成任务、忠于本岗位职守，能够在本岗位上面维护公司利益","cate":"优秀","score":"5","zsld":"1","qtld":"2","bmzj":"3","rowspan":"","isHidden":false
				},{
					"id":"10","type":"责任感15%","content":"交付的工作需要督促才能完成、在公司督促下能够维护公司利益","cate":"优秀","score":"5","zsld":"1","qtld":"2","bmzj":"3","rowspan":"","isHidden":false
				},{
					"id":"11","type":"责任感15%","content":"敷衍了事、态度傲慢、无责任心、做事粗心大意、损害公司利益","cate":"优秀","score":"5","zsld":"1","qtld":"2","bmzj":"3","rowspan":"","isHidden":false
				}
				],
				newTableData:[],
				tipFlag:false,
				showType:[],
				showTypeS:''
			}
		},

		methods:{
			////数组对象排序 原理利用sort()
			listSort(prop){
				return function (obj1, obj2) {
					var val1 = obj1[prop];
					var val2 = obj2[prop];if (val1 < val2) {
						return -1;
					} else if (val1 > val2) {
						return 1;
					} else {
						return 0;
					}
				}
			},
			//// getRepeatNum 获取数组中重复值的个数
			getRepeatNum(titleList){
				let obj = {};
				let arr = [];
				let pushArr= []
				for(let i= 0, l = titleList.length; i< l; i++){
					let item = titleList[i].type;
					obj[item] = (obj[item] +1 ) || 1;
				}

				for(let j in obj){
					var indexOf4 = (titleList || []).findIndex((item) => item.type == j);
					arr.push(indexOf4);
				}

				return [obj,arr];
			},
			//// 更改数组对象中的元素
			getResetList(list){
				let objList = this.$options.methods.getRepeatNum(this.tableData.sort(this.$options.methods.listSort("type")));
				for (var i = 0;i < list.length;i++) {
					for(let j in objList[0]){
						if(list[i].type == j){
							list[i].rowspan = objList[0][j];
						}
					};
					for (let j in objList[1]) {
						if(i == objList[1][j]){
							list[i].isHidden = true;
						}
					};
				}
				return list;
			},
			tipShow(){
				this.tipFlag = true;
				setTimeout(()=>{
					this.tipFlag = false;
				},1500);
			}

		},

		created:function(){
			//// 转换数据格式
			this.newTableData = this.getResetList(this.tableData);
			///// 页面跳转后传参
			
			switch (this.$route.query.type){
				case 'zsld':
					this.showType = ["直属领导"];
					this.showTypeS = ["zsld"];
					break;
				case 'qtld':
					this.showType = ["其他领导"];
					this.showTypeS =["qtld"];
					break;	
				case 'bmzj':
					this.showType = ["直属领导","其他领导","部门总监"];
					this.showTypeS =['zsld','qtld','bmzj'];
					break;	
			}


		},
		beforeMount:function(){


		},
		mounted:function(){


		}
	}


	
</script>



