import Vue from 'vue'
import Router from 'vue-router'
import Cate from '@/components/jxkh/cate'
import Test from '@/components/jxkh/jxkh'

Vue.use(Router)

export default new Router({
  // mode:'history',
  routes: [
    {
      path: '/',
      name: 'cate',
      component: Cate
    },{
      path: '/cate/:type',
      component: Test
    },{
      path: '/jxkh',
      name: 'jxkh',
      component: Test
    }
  ]
})



