<template>
	<div class="tip-box">
		<div class="tip">
			{{msg}}
		</div>
	</div>

</template>
<script>
	export default{
		name:"tip",
		data(){
			return {
				msg:"提交成功"
			}
		}
	}

</script>
<style type="text/css">
.tip-box{
	position: fixed;
	top:10%;
	right: 6%;
	width: 100%;
	z-index: 99;
}
.tip{
	width: 60%;
	border: 1px solid #d6ebff;
	background-color: #3399ffcf;
	border-radius: 6px;
	color: #fff;
	font-size: 30px;
	float:right;
	padding:10px;
	text-align: center;
	letter-spacing:4px;
}

</style>


