<template>
	<div class="cate">
		<ul class="cate-body">
			<li class="cate-body-list" v-for="item in cate" :key="item.id">
				<router-link :to="{path:'/jxkh',query:{type:item.url}}" class="cate-list-href">
					<h5 class="cate-list-text">{{item.name}}</h5>
				</router-link>	
				
			</li>
		</ul>
	</div>

</template>

<style>
.cate{
	width: 100%;
	height: 100vh;
	position:relative;
	top:0;
	left:0;
	z-index: 1;
	padding:12px;
}
.cate-body{
	width: 80%;
	height:800px;
	background-color: #d9dae0;
	position:absolute;
	top:0;
	right:0;
	bottom:0;
	left:0;
	margin:auto;
	display: flex;
	justify-content: space-around;
	flex-wrap: wrap; 
	align-items: center;
	border-radius: 12px;
}
.cate-body-list{
	width: 40%;
	height:200px;
	background-color: #2c8ee3;
	font-size: 20px;
	border-radius: 12px;
}
.cate-list-href{
	width: 100%;
	height:100%;
	display: block;
	position:relative;
	top:0;
	left:0;
	color:#fff;
}
.cate-body-list:hover{
	background-color: #4ea8f5; 
}
.cate-list-text{
	position:absolute;
	top:50%;
	left:0;
	transform:translateY(-50%);
	padding:0 12px;
	font-size: 28px;
	text-align: center;
	width: 100%;
}
</style>

<script>
	
	export default {
		name: 'cate',
		data(){
			return{
				msg:"文字单位测试",
				cate:[
				{"id":"1","name":"直属领导","url":"zsld"},
				{"id":"1","name":"其他领导","url":"qtld"},
				{"id":"1","name":"部门总监","url":"bmzj"},
				{"id":"1","name":"绩效考核(公司通用)","url":"common"},
				{"id":"1","name":"项目考核","url":"project"},
				{"id":"1","name":"定岗定级考核","url":"assess"}
				]
			}
		},
		


	}
</script>



