<template>
	<div class="headBody">

		<h4 class='headBody-h3'>{{msg}}</h4>
		<ul class='headBody-title'>
			<li v-for="(item,index) in textData" :key="index">
				<span>{{item.title}}</span>：<span>{{item.data}}</span>
			</li>
		</ul>

	</div>
</template>

<style>
.headBody{
	width: 100%;
}
.headBody-h3{
	/*font-size: 40;*/
	width: 100%;
	text-align: center;
	padding:6px 0;
}
.headBody-title{
	width: 100%;
	font-size: 30px;
	padding:6px;
	overflow: hidden;
}
.headBody-title > li{
	width: 50%;
	/*line-height: 20px;*/
	text-indent: 6px;
	float:left;
	padding:4px 0;
}
</style>

<script>
	export default {
		name: 'headw',
		data(){
			return{
				msg:"日常行为考核表",
				textData:[
					{'title':'姓名','data':'张三'},
					{'title':'职位','data':'web前端'},
					{'title':'部门','data':'项目部'},
					{'title':'考核日期','data':'2020-04-01'}
				]
			}
		}


	}
</script>




